import React from 'react'
import './header.css'
import { Link } from 'react-scroll';
import { AppBar } from '@mui/material';

function Header() {
    return (
        <AppBar position='fixed'>
            <div className="header">
                <div className="header_left">
                    <h1>My-PortFolio</h1>
                </div>
                <div className='header_right'>
                    <Link to='home' smooth={true} durition={500}>
                        <h4>Home</h4>
                    </Link>
                    <Link to='skill' smooth={true} durition={500}>
                        <h4>Skills</h4>
                    </Link>
                    <Link to='project' smooth={true} durition={500}>
                        <h4>Project</h4>
                    </Link>
                    <Link to='contact' smooth={true} durition={500}>
                        <h4>Contact</h4>
                    </Link>
                    <Link to='exp' smooth={true} durition={500}>
                        <h4>Experience</h4>
                    </Link>
                </div>
            </div>
        </AppBar>
    )
}

export default Header;