export default function Project() {
  const [showAll, setShowAll] = useState(false);
  const displayedProjects = showAll ? projects : projects.slice(0, 3);

  return (
    <Element name="project">
      <Box id="projects" sx={{ bgcolor: "#0A192F", color: "#EDEDED", py: 5 }}>
        <Container maxWidth="lg">
          <Typography variant="h4" align="center" gutterBottom sx={{ color: "#4ECDC4", fontWeight: "bold" }}>
            Projects
          </Typography>
          <Grid container spacing={4} sx={{
            justifyContent: "center",
            alignItems: "center",
          }}>
            {displayedProjects.map((project, index) => (
              <Grid item xs={12} sm={6} md={4} key={index}  >
                <Card sx={{ bgcolor: "#112240", color: "#EDEDED", borderRadius: "12px", boxShadow: "0px 4px 10px rgba(78, 205, 196, 0.5)" }}>
                  <CardMedia component="img" height="180" image={photo} alt={project.title} />
                  <CardContent>
                    <Box display="flex" justifyContent="space-between" alignItems="center">
                      <Typography variant="h6" sx={{ fontWeight: "bold" }}>{project.title}</Typography>
                      <Box>
                        <IconButton component={Link} href={project.github} target="_blank" sx={{ color: "#EDEDED" }}>
                          <GitHubIcon />
                        </IconButton>
                        <IconButton component={Link} href={project.website} target="_blank" sx={{ color: "#EDEDED" }}>
                          <LanguageIcon />
                        </IconButton>
                      </Box>
                    </Box>
                    <Typography variant="body2" sx={{ mt: 1 }}>{project.description}</Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
          {/* {!showAll && (
          <Box display="flex" justifyContent="center" mt={4}>
            <Button variant="contained" sx={{ bgcolor: "#4ECDC4", color: "#0A192F", fontWeight: "bold" }} onClick={() => setShowAll(true)}>
              See All Projects
            </Button>
          </Box>
        )} */}
        </Container>
      </Box>
    </Element>
  );
}