// export default Contact;

import React from "react";
import { Box, Container, Typography, Grid, TextField, Button, IconButton, Link } from "@mui/material";
import { GitHub, LinkedIn, Email, Phone } from "@mui/icons-material";
import { Element } from "react-scroll";
export default function Contact() {
  return (
    <Element name="contact">
      <Box id="contact" sx={{ bgcolor: "#0A192F", color: "#EDEDED", py: 5 }}>
        <Container maxWidth="md">
          <Typography variant="h4" align="center" gutterBottom sx={{ color: "#4ECDC4", fontWeight: "bold" }}>
            Contact Me
          </Typography>

          <Grid container spacing={3} justifyContent="center" textAlign="center">
            <Grid item xs={12} sm={4}>
              <IconButton sx={{ color: "#4ECDC4" }}><Email /></IconButton>
              <Link href="mailto:msivaram02031999@gmail.com" underline="hover" sx={{ color: 'lightblue' }}> msivaram02031999@gmail.com </Link>
            </Grid>
            <Grid item xs={12} sm={4}>
              <IconButton sx={{ color: "#4ECDC4" }}><Phone /></IconButton>
              <Link href="tel:+919952452450" underline="hover" sx={{ color: 'lightblue' }}> 9952452450 </Link>
            </Grid>
            {/* <Grid item xs={12} sm={4}>
            <IconButton sx={{ color: "#4ECDC4" }}><LinkedIn /></IconButton>
            <Typography variant="body1">linkedin.com/in/balamurugan</Typography>
          </Grid> */}
          </Grid>

          <Box component="form" sx={{ mt: 4, bgcolor: "#112240", p: 4, borderRadius: "8px" }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField fullWidth label="Your Name" variant="outlined" InputLabelProps={{ style: { color: "#EDEDED" } }} sx={{ input: { color: "#EDEDED" } }} />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField fullWidth label="Your Email" variant="outlined" InputLabelProps={{ style: { color: "#EDEDED" } }} sx={{ input: { color: "#EDEDED" } }} />
              </Grid>
              <Grid item xs={12}>
                <TextField fullWidth label="Subject" variant="outlined" InputLabelProps={{ style: { color: "#EDEDED" } }} sx={{ input: { color: "#EDEDED" } }} />
              </Grid>
              <Grid item xs={12}>
                <TextField fullWidth label="Message" variant="outlined" multiline rows={4} InputLabelProps={{ style: { color: "#EDEDED" } }} sx={{ input: { color: "#EDEDED" } }} />
              </Grid>
            </Grid>
            <Button variant="contained" sx={{ mt: 3, bgcolor: "#4ECDC4", color: "#0A192F", fontWeight: "bold", px: 4 }}>
              Send Message
            </Button>
          </Box>

          {/* Social Media Links */}
          <Box mt={4} textAlign="center">
            <IconButton sx={{ color: "#4ECDC4", mx: 1 }} href="https://github.com/balamurugan">
              <GitHub />
            </IconButton>
            <IconButton sx={{ color: "#4ECDC4", mx: 1 }} href="https://www.linkedin.com/in/srx-undefined-06ab07250/">
              <LinkedIn />
            </IconButton>
          </Box>
        </Container>
      </Box>
    </Element>
  );
}
