import React, { useState } from "react";
import { Box, Container, Typography, Grid, Paper, Button } from "@mui/material";

export default function Resume() {
  const [selectedSection, setSelectedSection] = useState(null);

  const sections = {
    Education: (
      <Paper sx={{ p: 2, bgcolor: "#112240" }}>
        <Typography variant="h6">Bachelor's of Commerce(Computer Application)</Typography>
        <Typography variant="body2" color="gray">St.Xavier's College | 2017 - 2020</Typography>
        <Typography variant="body1" mt={1}>
          Specialization in Full Stack Web Development.
        </Typography>
      </Paper>
    ),
    "Professional Skills": (
      <Grid container spacing={2} justifyContent="center" textAlign="center">
        {["HTML & CSS", "JavaScript", "Postman", "SQL & Databases"].map((skill, index) => (
          <Grid item xs={12} sm={4} md={2} key={index}>
            <Paper sx={{ p: 1.5, px: 3, bgcolor: "#112240", textAlign: "center", borderRadius: "8px" }}>
              <Typography variant="body1" sx={{ fontWeight: "bold" }}>{skill}</Typography>
            </Paper>
          </Grid>
        ))}
      </Grid>
    ),
    Experience: 
      (
        <Paper sx={{ p: 2, bgcolor: "#112240" }}>
        <Typography variant="h6">HR</Typography>
        <Typography variant="body2" color="gray"> Gove | 2025 - 2026 Present</Typography>
        <Typography variant="body1" mt={1}>
          Handle organization process and documentation
        </Typography>
        </Paper>
            )
    
  };

  return (
    <Box id="resume" sx={{ bgcolor: "#0A192F", color: "#EDEDED", py: 5 }}>
      <Container maxWidth="lg">
        {/* Section Title */}
        <Typography variant="h4" align="center" gutterBottom sx={{ color: "#4ECDC4", fontWeight: "bold" }}>
          Resume
        </Typography>

        <Grid container spacing={2} justifyContent="center" mb={3}>
          {Object.keys(sections).map((section) => (
            <Grid item key={section}>
              <Button
                variant={selectedSection === section ? "contained" : "outlined"}
                sx={{ color: "#4ECDC4", borderColor: "#4ECDC4", fontWeight: "bold" }}
                onClick={() => setSelectedSection(selectedSection === section ? null : section)}
              >
                {section}
              </Button>
            </Grid>
          ))}
        </Grid>

        {selectedSection && <Box mt={3} textAlign="center">{sections[selectedSection]}</Box>}
      </Container>
    </Box>
  );
}
