// export default Experience

import React from "react";
import { Container, Typography, Grid, Card, CardContent } from "@mui/material";
import { Element } from "react-scroll";
import { motion } from "framer-motion";

const experiences = [
  {
    company: "The Chennai Skills",
    role: "Accountant",
    duration: "DEC 2023 - Present",
    description: "Handling monthly budget and Financial work ",
  },

];

function Experience() {
  return (
    <Element name="exp">
      <Container id="experience" sx={{ padding: 4, textAlign: "center" }}>
        <Typography variant="h4" gutterBottom>
          Experience
        </Typography>

        <Grid container spacing={3} justifyContent="center">
          {experiences.map((exp, index) => (
            <Grid item xs={12} md={6} lg={4} key={index}>
              <motion.div
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 1, delay: index * 0.2 }}
              >
                <Card sx={{ backgroundColor: "#1e1e1e", color: "white", borderRadius: 2, maxWidth: "800px" }}>
                  <CardContent>
                    <Typography variant="h6" color="primary">{exp.company}</Typography>
                    <Typography variant="subtitle1">{exp.role}</Typography>
                    <Typography variant="body2" color="gray">{exp.duration}</Typography>
                    <Typography variant="body1" mt={1}>{exp.description}</Typography>
                  </CardContent>
                </Card>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Element>
  );
}

export default Experience;
