import React, { useEffect, useState } from "react";
import { Box, Container, Grid, Typography } from "@mui/material";
import profile from "../../assets/profile/IMG_20230330_143220.png"
import { Element } from "react-scroll";
export default function Home() {
  const [isVisible, setIsVisible] = useState(true);

  useEffect(() => {
    const interval = setInterval(() => {
      setIsVisible((prev) => !prev);
    }, 1000);

    return () => clearInterval(interval);
  }, []);
  return (
    <>
      <Element name="home">
        <Box sx={{ bgcolor: "#0A192F", height: "100vh", display: "flex", alignItems: "center", justifyContent: "center" }}>
          <Container maxWidth="lg">
            <Grid container spacing={2} alignItems="center">
              <Grid item xs={12} md={6}>
                <Box
                  component="img"
                  src={profile}
                  alt="Profile Picture"
                  sx={{ width: "50%", height: "auto", alignContent: "center", marginLeft: "70px", border: "5px solid skyblue", borderRadius: "10px" }}
                />
              </Grid>
              {/* Right Column - Text Content */}
              <Grid item xs={12} md={6} sx={{ color: "white", textAlign: "left" }}>
                <Typography variant="h2" gutterBottom>
                  Welcome to my world
                </Typography>
                <Typography variant="h3" gutterBottom>
                  Hi, I'm <b>Sivaram</b>
                </Typography>
                <Typography gutterBottom variant="h6" sx={{ opacity: isVisible ? 1 : 0, transition: "opacity 0.5s" }}>
                  Full Stack Web Developer
                </Typography>


                <br></br>
                <Typography variant="body1">
                  <p><span style={{ color: "red", fontSize: '20px' }}>P</span>assionate Full Stack Developer with expertise in designing, developing, and deploying scalable web applications.
                    Proficient in front-end and back-end technologies, including React, Node.js, and databases like MongoDB and PostgreSQL.
                    Adept at problem-solving, optimizing performance, and collaborating in agile teams to deliver high-quality software solutions.
                    Seeking to leverage technical skills and innovation to contribute to dynamic development projects and enhance user experiences.</p>
                </Typography>
              </Grid>
            </Grid>
          </Container>
        </Box>
      </Element>
    </>
  )

}